$small-breakpoint:   400px;
$medium-breakpoint:  960px;
$wide-breakpoint:    1366px;

// $sf-font: 'SF Pro Display' !default;

$primary-color: #000000 !default;

$placeholder-color: #949494 !default;

$gray: #373737 !default;
$dark-gray: #282828 !default;
$light-blue: #1292e8 !default;

// Pixel to rem
$ptr: 0.0625rem;

// @font-face{
//     font-family: $sf-font;
//     src: url('/assets/fonts/sfprodisplaylight.otf') format('opentype'),
//          url('/assets/fonts/sfprodisplaylight.ttf') format('truetype');  /* Safari, Android, iOS */
